@use '../base/variables' as v;
@use '../base/mixins' as m;

.header {
    padding: 5rem 0;
    position: relative;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;

    @include m.tablet {
        min-height: 70rem;
    }

    &::before {
        content: "";
        background-color: v.$black;
        opacity: .6;
        position: absolute;
        top: 0;
        left: 0;
        bottom: 0;
        right: 0;

    }

    .header-content {
        position: relative;

        h1 {
            text-align: center;
            color: v.$withe;
            text-transform: uppercase;
            margin-top: 5rem;

            @include m.tablet {
                margin-top: 10rem;
            }
        }
    }

}


.header-inicio {
    background-image: url(../img/header_inicio.jpg);
}

.header-nosotros {
    background-image: url(../img/header_nosotros.jpg);
}

.header-proceso {
    background-image: url(../img/header_proceso.jpg);
}

.header-menu {
