@use './variables' as v;

@mixin phone {
    @media(min-width: v.$phone) {
        @content;
    }
}

@mixin tablet {
    @media(min-width:v.$tablet) {
        @content;
    }
}

@mixin desktop {
    @media(min-width:v.$desktop) {
        @content;
    }
}

@mixin grid ($cols, $colgap) {
    display: grid;
    grid-template-columns: repeat($cols, 1fr);
    gap: $colgap;
}